/* 运行按钮样式 */
.run-button{
    background: rgb(71, 71, 71);
    color: rgb(240, 240, 240);
    font-size: 10px;
    font-weight: 600;
    text-align: center;
    cursor: pointer;
}
.run-button:hover{
    background: rgb(90, 90, 90);
}

/* CodeMirror样式 */
#CodeMirror-container .ReactCodeMirror{
    height: 100%;
    /* 设置代码的字号、粗细等 */
    font-size: 16px;
    font-weight: 600;
    font-family: consolas;
}

#CodeMirror-container .CodeMirror{
    height: 100%;
    font-size: 16px;
    font-weight: 600;
    font-family: consolas;
}

/* 悬浮框题目样式 */
.Select-border{
    text-align: center;
    font-size: 22px;
    font-weight: 700;
}

/* 返回的叉号按钮 */
.ButtonBack{
    /* background: rgb(222, 225, 230); */
    cursor: pointer;
    border-radius: 50px;
}

.ButtonBack:hover{
    background: rgb(199, 202, 207);
}

/* 选择Algorithm或者Model的框的悬浮高亮 */
.Show-Item:hover{
    background: #57FF8C;
}